/* 
	-------------------------------------------
	BUTTONS
	-------------------------------------------
*/

.proj-btn {
	padding: 8px 19px;
	text-decoration: none;
	font-size: rem(16);
	font-family: $semi;
	color: #f6efd1;
	border: 3px solid #f6efd1;
	border-radius: 20px;
	transition: all 100ms ease-in;
	cursor: pointer;
	outline: none;

	&:hover,
	&:focus {
		background-color: #f6efd1;
		color: #3e3e3e;
	}

	&.white-btn {
		color: #fff;
		border: 3px solid #fff;

		&:hover,
		&:focus {
			color: #26292c;
			border-color:  #f6efd1;
		}
	}

	&.modal-btn {
		font-size: rem(18);
		padding: 4px 42px;
	}


	&.green {
		background-color: $green;
		border-color: $green;
		color: #fff;

		&.green-big {
			padding: 10px 24px;
			font-size: rem(20);
		}

		&:hover,
		&:focus {
			background-color: #35a633;
			border-color: #35a633;
		}

	}
}



//--------------------------------------
// ### MEDIA QUERIES ###
//--------------------------------------
@include breakpoint($mobile) {

	.proj-btn {
		padding: 8px 19px;
		text-decoration: none;
		font-size: rem(15);
		font-family: $semi;
		color: #f6efd1;
		border: 3px solid #f6efd1;
		border-radius: 20px;
		transition: all 100ms ease-in;

		&.green {
			&.green-big {
				font-size: rem(16);
			}
		}

	}

}