/* 
	-----------------------------------------------------
	SOCIAL LINKS
	-----------------------------------------------------
*/


.pizza-social {
	margin: 0;
	padding: 0;
	list-style: none;
	display: flex;
	
	&.header-social {
		padding-top: 25px;
	}

	&.footer-social {
		padding-top: 6px;
	}

	li:last-child {
		margin-right: 0;
	}

}

.pizza-social__item {
	margin-right: 9px;
}

.pizza-social__link {
	display: inline-block;
	
	text-decoration: none;
	color: #fff;

	&.inst {
		@include sprite($icon-ic-inst);

		&:hover,
		&:focus {
			@include sprite($icon-ic-inst-hover);
		}
	}

	&.vk {
		@include sprite($icon-ic-vk);

		&:hover,
		&:focus {
			@include sprite($icon-ic-vk-hover);
		}
	}

	&.fb {
		@include sprite($icon-ic-fb);

		&:hover,
		&:focus {
			@include sprite($icon-ic-fb-hover);
		}
	}
}



//--------------------------------------
// ### MEDIA QUERIES ###
//--------------------------------------
@include breakpoint($mobile) {
	.pizza-social {
		&.header-social {
			display: none;
		}
	}
}