.page__content__bg-pic {

	// max-width: 1450px;
	// width: 100%;

	width: 1114px;
	height: 457px;
	position: absolute;
	top: 0;
	left: 50%;
	z-index: -1;

	transform: translateX(-50%);

	background-image: url(../img/png/bg-pizza.png);
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;
}




// ###       ###
//------------------------------------------------
.pizza-content__bg-img {
	position: absolute;
	top: 200px;
	left: 0;
	width: 1100px;
	height: 1500px;
	background-image: url(../img/png/bg-logo.png);
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;

}




//--------------------------------------
// ### MEDIA QUERIES ###
//--------------------------------------
@media all and (max-width: 1140px) {
	.page__content__bg-pic {
		display: none;
	}

	.page-wrap {
		 background-image: url(../img/png/bg-pizza-desctop.png);
		background-repeat: no-repeat;
		background-position: 50% 0%;
		background-size: contain;
	}

}

@media all and (max-width: 768px) {

	.page-wrap {
		background-position: 50% 10%;
		background-size: 140%;
	}

}


@media all and (max-width: 550px) {

	.page-wrap {
		background-position: 50% 20%;
		background-size: 160%;
	}

}