    // url("../fonts/MyriadProLight/MyriadProLight.eot?#iefix")format("embedded-opentype"),
    // url("../fonts/MyriadProLight/MyriadProLight.woff") format("woff"),
    url("../fonts/MyriadProLight/MyriadProLight.ttf") format("truetype");
    font-style: normal;
    font-weight: normal;
}


