/* 
	-----------------------------------------------------
	PIZZA MODAL
	-----------------------------------------------------
*/

.pizza-modal {
	width: 700px;
	background-color: #fff;
	
	.remodal-close {
		background-color: transparent;
		border: none;
		cursor: pointer;
		outline: none;
		@include sprite($icon-close);
	}

}




// ###  ###
//--------------------------------------
.pizza-modal__title-wrap {
	display: flex;
	justify-content: space-between;
	padding: 30px 40px;
	background-color: $red;

}

.pizza-modal__title {
	font-family: $bold;
	font-size: rem(28);
	line-height: 26px;
	color: white;
	text-transform: uppercase;
}



// ###   ###
//--------------------------------------
.pizza-modal__descr-wrap {
	padding: 30px 40px 15px;
	text-align: left;
}

.pizza-modal__descr {
	font-family: $semi;
	font-size: rem(18);
	color: #222;
	line-height: 26px;
}

.pizza-modal__descr-title-wrap {
	display: flex;
	justify-content: space-between;
}

.pizza-modal__descr-title {
	display: inline-block;
	font-family: $light;
	font-size: rem(16);
	color: #787878;
	line-height: 26px;
	vertical-align: middle;

	&.name {
		max-width: 270px;
		width: 100%;
	}

	&.sum {
		max-width: 150px;
		width: 100%;
	}

	&.price {
		max-width: 125px;
		width: 100%;
	}

	&.total {
		max-width: 70px;
		width: 100%;
	}

}


//-------------------------------------------
// ###   /   ###
//-------------------------------------------
.pizza-modal__order-price-wrap {
	padding: 0px 40px;
	background-color: #f8fafa;
}


.pizza-modal__item-list {
	margin: 0;
	padding: 0;
	list-style: none;
	padding-top: 20px;
	padding-bottom: 20px;
	
	border-bottom: 1px solid #cadfdf;


	li:last-child {
		margin-bottom: 0;
		border-bottom: none;
	}
}


.pizza-modal__item {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 10px;
}




// ###  ###
//--------------------------------------
.pizza-modal__item-name {
	max-width: 270px;
	width: 100%;
	font-size: rem(16);
	line-height: 26px;
	color: #222;
	text-align: left;
}



// ###  ###
//--------------------------------------
.pizza-modal__item-count {
	max-width: 150px;
	width: 100%;
	text-align: left;
	.count-fild {
		display: inline-block;
		width: 40px;
		height: 36px;
		margin: 0 7px;
		font-size: rem(20);
		line-height: 26px;

		background-color: white;
		border: 1px solid #b9b9b9;
		border-radius: 16px;
		color: #2b2c2e;
		text-align: center;
		text-transform: uppercase;
		vertical-align: middle;
		outline: none;
	}
	
	.count-minus,
	.count-plus {
		display: inline-block;
		width: 20px;
		height: 20px;
		border: none;
		background-color: transparent;
		cursor: pointer;
		vertical-align: middle;
		outline: none;
		border-radius: 50%;
		transition: all 100ms ease-in;
		user-select: none;

		&:hover,
		&:focus {
			// background-color: lighten(#8b8b8b, 20%);
		}
	}

	.count-plus {
		@include sprite($icon-plus_green);
	}

	.count-minus {
		@include sprite($icon-minus_green);
	}
}


// ###  /   ###
//--------------------------------------
.pizza-modal__item-piece-price {
	max-width: 125px;
	width: 100%;
	text-align: left;
}

.pizza-modal__item-price-discount {
	font-size: rem(22);
	line-height: 26px;
	color: $green;
	text-transform: uppercase;
	vertical-align: middle;
	margin-right: 14px;
}

.pizza-modal__item-price {
	font-family: $light;
	font-size: rem(17);
	line-height: 26px;
	text-transform: uppercase;
	color: #8b8b8b;
	vertical-align: middle;
}


.pizza-modal__item-total {
	max-width: 70px;
	width: 100%;
	text-align: left;
	font-family: $light;
	font-size: rem(20);
	line-height: 26px;
	text-transform: uppercase;
	color: #222;
}


// ###  /  ###
//-------------------------------------------
.pizza-modal__price-line {
	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 10px 0;



	.discount-wrap {
		padding-top: 10px;
		padding-bottom: 10px;
		color: #222;
		line-height: 26px;
	}

	.discount-text {
		margin-right: 10px;
		font-size: rem(18);
		vertical-align: middle;
	} 

	.discount-num {
		padding: 5px 15px;
		font-size: rem(24);
		vertical-align: middle;

		border: 1px solid #3ec03b;
		border-radius: 17px;
	}


	.total-sum-wrap {
	
	}

	.total-sum__text {
		display: inline-block;
		margin-right: 20px;
		font-size: rem(18);
		vertical-align: middle;
	}

	.total-sum {
		display: inline-block;
		margin-right: 10px;
		font-size: rem(28);
		color: $green;
		line-height: 26px;
		text-transform: uppercase;
		vertical-align: middle;
	}


}



// ###  ###
//-------------------------------------------
.pizza-modal__pay-wrap {

	padding: 25px 40px 18px;
	border-bottom: 1px solid #b6b6b6;
}


.pizza-modal__pay-title {
	width: 100%;
	margin-bottom: 20px;
	text-align: left;
	color: #222;
	font-family: $semi;
	font-size: rem(18);
	line-height: 26px;
}


.pizza-modal__pay-block-wrap {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}

.pizza-modal__pay-label {
	cursor: pointer;
	display: flex;
	text-align: left;

	input {
		display: none;
		&:checked ~ .circle {
			.check {
				opacity: 1;
			}
		}
	}

	.circle {
		display: block; 
		width: 24px;
		height: 24px;
		margin-right: 12px;
		border: 1px solid #34a532;
		border-radius: 50%;
		vertical-align: middle;

		position: relative;
	}

	.check {
		display: inline-block;
		width: 10px;
		height: 10px;
		background-color: #4d5a5e;
		border-radius: 50%;

		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);

		transition: all 100ms ease-in;
		opacity: 0;
	}


	.text-wrap {
		display: block;
	}

	.text {
		display: inline-block;
		margin-bottom: 5px;
		color: #3a3a3a;
		font-size: rem(18);
		line-height: 26px;
	}

	.text-small {
		font-family: $light;
		font-size: rem(16);
		color: #222;
		line-height: 10px;
		span {
			display: block;
		}
	}

}



// ###   ###
//-------------------------------------------
.pizza-modal__info {
	padding: 25px 40px 18px;
}

.pizza-modal__info-title {
	width: 100%;
	margin-bottom: 20px;
	text-align: left;
	color: #222;
	font-family: $semi;
	font-size: rem(18);
	line-height: 26px;
	text-align: left;
}


.pizza-modal__input-wrap {
	display: flex;
	justify-content: space-between;
	flex-direction: row;
}

.pizza-modal__input {
	width: 100%;


	&.input-small {
		max-width: 300px;
		width: 100%;
		margin-bottom: 20px;
	}

	input {
		display: block;
		width: 100%;
		padding: 8px 20px;
		border: 1px solid #3ec13b;
		border-radius: 20px;
		outline: none;

		&.is-active {
			background-color: #f1f1f1;
			border-color: #f1f1f1;
		}
	}
}



// ###  /    ###
//-------------------------------------------
.pizza-modal__social-button {
	display: flex;
	justify-content: space-between;
	align-items: center;

	padding: 10px 40px 25px;
}


.pizza-modal__social-wrap {

}

.pizza-modal__social-title {
	margin-right: 15px;
	font-size: rem(18);
	font-family: $light;
	vertical-align: middle;
}

.pizza-modal__social {
	display: inline-block;
	vertical-align: middle;
	border-radius: 50%;
	transition: all 100ms ease-in;
	width: 28px;
	height: 28px;
	border: 1px solid $green;
	position: relative;

	&:hover,
	&:focus {
		background-color: $green;
		
	}

	&.vk,
	&.fb {
		svg {
			width: 15px;
			height: 15px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			transition: all 100ms ease-in;
			fill: #222;
		}

		&:hover,
		&:focus {
			svg {
				fill: #fff;
			}
		}

	}

	&.vk {
		margin-right: 8px;
	}

}




//--------------------------------------
// ### MEDIA QUERIES ###
//--------------------------------------
@include breakpoint($mobile) {

	.pizza-modal {
		max-width: 340px;
		// width: 100%;

	}


	// ###  ###
	//--------------------------------------
	.pizza-modal__title-wrap {
		padding: 20px 10px;
	}

	.pizza-modal__title {
		font-size: rem(18);
		line-height: 26px;

	}


	//--------------------------------------
	.pizza-modal__descr-wrap {
		padding: 20px 10px 20px;
		text-align: left;
	}

	.pizza-modal__descr-title-wrap {
		display: none;
	}

	//-------------------------------------------
	.pizza-modal__order-price-wrap {
		padding: 0px 10px;
	}


		

	//--------------------------------------
	.pizza-modal__item-list {
		padding-top: 20px;
		padding-bottom: 20px;
		
		border-bottom: none;
	}

	.pizza-modal__item {
		display: block;
		margin-bottom: 5px;
		padding-bottom: 10px;
		border-bottom: 1px solid #cadfdf;
	}


	//--------------------------------------
	.pizza-modal__item-name {
		display: block;
		max-width: auto;
		width: 100%;
		margin-bottom: 7px;
	}


	//--------------------------------------
	.pizza-modal__item-count {
		display: inline-block;
		max-width: auto;
		width: 36%;
		text-align: center;
	}

	.pizza-modal__item-piece-price {
		display: inline-block;
		max-width: auto;
		width: 38%;
		text-align: center;
	}

	.pizza-modal__item-total {
		display: inline-block;	
		max-width: auto;
		width: 19%;
		text-align: center;
	}


	//-------------------------------------------
	.pizza-modal__price-line {
		display: flex;
		background-color: #fff;
		padding: 20px 0;



		.discount-wrap {
			width: 100%;
			padding-bottom: 0px;
			text-align: left;

		}

		.discount-text {
			margin-right: 0px;
			font-size: rem(16);
		} 

		.discount-num {
			padding: 5px 15px;
			font-size: rem(16);
			border: none;
			border-radius: none;
			color: $green;
		}

		.total-sum-wrap {
			display: block;
			width: 100%;
		}

		.total-sum {
			font-size: rem(22);
		}

	}

	//-------------------------------------------
	.pizza-modal__pay-wrap {
		padding: 20px 10px 20px;
	}

	.pizza-modal__pay-title {
		margin-bottom: 10px;
		font-size: rem(16);
	}


	.pizza-modal__pay-block-wrap {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		flex-direction: column;
	}

	
	.pizza-modal__pay-block {
		margin-bottom: 15px;
	}

	.pizza-modal__pay-label {
		.text {
			display: inline-block;
			margin-bottom: 0px;
			color: #3a3a3a;
			font-size: rem(16);
		}
		.text-small {
			font-size: rem(14);
		}
	}


	//-------------------------------------------
	.pizza-modal__info {
		padding: 20px 10px 20px;
	}

	.pizza-modal__input-wrap {
		display: flex;
		justify-content: space-between;
		flex-direction: column;
	}

	.pizza-modal__input {
		&.input-small {
			max-width: 100%;
			width: 100%;
			margin-bottom: 10px;
		}
	}


	//-------------------------------------------
	.pizza-modal__social-button {
		flex-direction: column;
		padding: 0px;
	}

	.pizza-modal__button-wrap {
		order: 1;
		margin-bottom: 15px;
	}

	.pizza-modal__social-wrap {
		order: 2;
		width: 100%;
		padding: 20px 0;
		background-color: #f8fafa;
	}



}