/* 
	-----------------------------------------------------
	ORDER LINE
	-----------------------------------------------------
*/


.order-line {
	margin-bottom: 20px;


	&.is_stuck {
		z-index: 10;
		
	}


}


.order-line-wrap {
	display: flex;
	justify-content: space-between;
	padding: 20px 30px;
	color: #fff;
	background-color: rgba(#4f5254, 0.8);
}



//--------------------------------------
// ###   ###
//--------------------------------------
.order-line__left-side {
	// max-width: 695px;
	width: 60%;
	// margin-right: 30px;
}



// ###  ###
//--------------------------------------
.order-line__bar {
	height: 34px;
	margin-bottom: 13px;
	background-color: white;
	border-radius: 16px;
	position: relative;
}

.order-line__line {
	width: 20%;
	height: 34px;
	background-color: $green;
	border-radius: 16px;
}



// ###    ###
//--------------------------------------
.order-line__piece-social-wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
}


// ###  ###
//--------------------------------------
.order-line__piece-wrap {
	font-family: $light;
	font-size: rem(18);
	line-height: 22px;
}

.order-line__number-pieces {
	color: $green;
	font-style: italic;
}


// ###  ###
//--------------------------------------

.order-line__social-title {
	margin-right: 15px;
	font-size: rem(18);
	font-family: $light;
	vertical-align: middle;
}

.order-line__social {
	display: inline-block;
	vertical-align: middle;
	border-radius: 50%;
	transition: all 100ms ease-in;
	width: 28px;
	height: 28px;
	border: 1px solid $green;
	position: relative;

	&:hover,
	&:focus {
		background-color: #1f2021;
	}

	&.vk,
	&.fb {
		svg {
			width: 15px;
			height: 15px;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);

			fill: #fff;
		}

	}

	&.vk {
		margin-right: 8px;
	}
}





//--------------------------------------
// ###   ###
//--------------------------------------
.order-line__right-side {
	// width: 40%;
}

// ### / ###
//--------------------------------------
.order-line__piece-descout-wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 14px;
	font-size: rem(18);
	font-family: $light;
}


// ###  ###
//--------------------------------------
.order-line__piece-discount {
	margin-right: 45px;
	.how-much {
		font-family: $semi;
	}
}


// ###  ###
//--------------------------------------
.order-line__discout-wrap {
	
}

.order-line__discout-title {
	margin-right: 18px;
	vertical-align: middle;
}

.order-line__discout-number {
	display: inline-block;
	width: 66px;
	height: 40px;
	font-family: $reg;
	font-size: rem(24);
	border: 2px solid #3ec03b;
	border-radius: 17px;
	text-align: center;
	vertical-align: middle;
}



// ### / ###
//--------------------------------------
.order-line__price-button-wrap {
	display: flex;
	justify-content: space-between;
	align-items: center;
	font-size: rem(18);
	font-family: $light;
}



// ###  ###
//--------------------------------------
.order-line__price-wrap {
	margin-right: 10px;
}


.order-line__price-text {
	margin-right: 7px;
}

.order-line__price-total {
	font-family: $reg;
	font-size: rem(26);
	text-transform: uppercase;
	color: $green;
}






//--------------------------------------
// ### MEDIA QUERIES ###
//--------------------------------------
@include breakpoint($tablet) {
	

	// ###    ###
	//--------------------------------------
	.order-line__piece-social-wrap {
		// justify-content: space-between;
		flex-direction: column;
		align-items: flex-start;
	}


	// ###   ###
	//--------------------------------------
	.order-line__piece-wrap {
		margin-bottom: 8px;
		font-size: rem(15);
	}



	// ### / ###
	//--------------------------------------
	.order-line__piece-descout-wrap {
		margin-bottom: 0px;
	}
	
	// ###  ###
	//--------------------------------------
	.order-line__piece-discount {
		margin-right: 10px;

	}



	// ### / ###
	//--------------------------------------
	.order-line__price-button-wrap {
		flex-direction: column;
		align-items: flex-start;
	}

	// ###  ###
	//--------------------------------------
	.order-line__price-wrap {
		margin-right: 0px;
		margin-bottom: 17px;
	}


	


}


@include breakpoint($mobile) {


	.order-line-wrap {
		padding: 6px 25px 14px;
	}
	
	//--------------------------------------
	// ###   ###
	//--------------------------------------
	.order-line__left-side {
		display: none;
	}


	//--------------------------------------
	// ###   ###
	//--------------------------------------
	.order-line__right-side {
		width: 100%;
	}


	// ###  ###
	//--------------------------------------
	.order-line__piece-discount {
		font-size: rem(16);
	}

	.order-line__discout-title {
		margin-right: 0;
	}

	.order-line__discout-number {
		width: 66px;
		height: 40px;
		border: none;
		color: #3ec03b;
	}


	// ### / ###
	//--------------------------------------
	.order-line__price-wrap {
		margin-bottom: 0;
	}

	.order-line__price-button-wrap {
		flex-direction: row;
		align-items: center;
	}

	// ###  ###
	//--------------------------------------
	.order-line__price-text {
		font-size: rem(16);
	}
	.order-line__price-total {
		display: block;
		line-height: 20px;
	}




}
