/* 
	-----------------------------------------------------
	MAIN HEADER
	-----------------------------------------------------
*/

.main-header {
	max-height: 80px;
	margin-bottom: 100px;
	background-color: rgba($red, 0.94);
	color: #fff;
	position: relative;
}


.main-header-wrap {
	display: flex;
	justify-content: space-between;
}



// ###  ###
//--------------------------------------
.header-logo {
	width: 150px;
	height: 80px;
	padding-top: 15px;
	background-color: #f4f4f4;
	text-align: center;	
	position: relative;

	img {
		display: inline-block;
	}

}


// ###  ###
//--------------------------------------
.header-title {
	padding-top: 30px;
	font-size: rem(26);
	line-height: 24px;
	text-transform: uppercase;
	font-family: $light;

	.bold {
		font-family: $bold;
	}
}


.header-title__mobile {
	display: none;
	width: 100%;
	padding: 17px 10px;
	font-size: rem(22);
	line-height: 24px;
	text-transform: uppercase;
	font-family: $light;
	background-color: $red;
	text-align: center;

	.bold {
		font-family: $bold;
	}
}




// ###  ###
//--------------------------------------
.header-phone {
	padding-top: 15px;

}

.header-phone__icon {
	display: inline-block;
	margin-right: 10px;
	vertical-align: middle;
	@include sprite($icon-ic-phone);
}

.header-phone__phone {
	font-family: $semi;
	font-size: rem(19);
	color: #fff;
	text-transform: uppercase;
	text-decoration: none;
	vertical-align: middle;
}

.header-phone__descr {
	font-family: $reg;
	font-size: rem(16);
}


.header-button-wrap {
	padding-top: 30px;
}



// ### /  1200px ###
//--------------------------------------
.header-button-phone {
	display: none;

	.button {
		padding: 0 15px;
		background-color: #19202a;
		text-align: center;
		font-family: $light;
		font-size: rem(12);
		line-height: 12px;
		color: #fff;
		text-decoration: none;
		vertical-align: middle;
		transition: all 100ms ease-in;

		&:hover,
		&:focus {
			background-color: #242d38;
		}

		.icon {
			padding-top: 15px;
			margin-bottom: 5px;
			text-align: center;
			&:after {
				content: '';
				display: inline-block;
				@include sprite($icon-ic-phone);
			}
		}
	}

	
	.phone {
		padding-top: 15px;
		padding-left: 25px;
		padding-right: 25px;
		background-color: #fff;
		color: #323232;
		text-align: center;

		a {
			display: inline-block;
			color: #323232;
			text-decoration: none;
			font-size: rem(19);
			font-family: $semi;
			text-transform: uppercase;	
		}

		.descr {
			font-size: rem(14);
			font-family: $light;
			line-height: 15px;
		}
	}


}



//--------------------------------------
// ### MEDIA QUERIES ###
//--------------------------------------
@include breakpoint($desktop) {
	
	// ###  ###
	//--------------------------------------
	.header-logo {
		width: 123px;
		position: relative;
		img {
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			margin: auto;
			max-width: 62px;
			max-height: 100%;

		}
	}
	
	// ### / ###
//--------------------------------------
	.header-phone,
	.header-button-wrap {
		display: none;
	}

	
	// ### /  1200px ###
	//--------------------------------------
	.header-button-phone {
		display: flex;
	}
}



@include breakpoint($tablet) {
	.main-header {
		margin-bottom: 30px;
	}


	// ###  ###
	//--------------------------------------
	.header-title {
		padding-top: 20px;
		font-size: rem(22);
		line-height: 24px;
		text-transform: uppercase;
		font-family: $light;

		.bold {
			display: block;
		}
	}

}


@include breakpoint($mobile) {
	
	.main-header {
		margin-bottom: 90px;
	}


	.main-header-wrap {}


	// ###  ###
	//--------------------------------------
	.header-logo {
		width: 58px;
		background-color: #fff;

		img {
			max-width: 58px
		}
	}

	
	// ###  ###
	//--------------------------------------
	.header-title {
		display: none;
	}

	.header-title__mobile {
		display: block;
	}


	// ### /  1200px ###
	//--------------------------------------
	.header-button-phone {
		width: 100%;
		.button {
			width: 20%;
			order: 1;
		}
		
		.phone {
			width: 80%;
			padding-left: 0;
			padding-right: 0;
		}
	}


}

