/* 
	-----------------------------------------------------
	PIZZA
	-----------------------------------------------------
*/

.pizza-content {
	padding-bottom: 50px;
	position: relative;

}



@media all and (max-width: 1150px) {
	.pizza-content__bg-img {
		display: none;
	}
}



// ###  ###
//--------------------------------------
.pizza-list {
	margin: 0;
	padding: 0;
	list-style: none;

	margin-bottom: 45px;
	@include clearfix;

}


// ### Item ###
//--------------------------------------
.pizza-item {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	margin-bottom: 20px;
	padding: 40px 25px;
	background-color: #fff;
	@include span(4);

}



.pizza-item__price-count-wrap {
	max-width: 270px;
	width: 100%;
	margin: 0 auto;
}


.pizza-item__text {
	display: inline-block;
	margin-right: 30px;
	color: #28292a;
	font-family: $light;
	font-size: rem(18);
	font-weight: 300;
	line-height: 26px;
	vertical-align: middle;

	&.text--m-right-small {
		margin-right: 19px;
	}
}



// ###  ###
//--------------------------------------
.pizza-item__img {
	width: 310px;
	height: 310px;
	// max-width: 310px;
	// max-height: 310px;
	// width: 100%;
	// height: 100%;
	position: relative;
	text-align: center;
	margin-bottom: 30px;
	position: relative;

	img {
		position: absolute;
		top: 0;
		right: 0;
		left: 0;
		bottom: 0;
		margin: auto;
		max-width: 100%;
		max-height: 100%;
	}
}


// ###  ###
//--------------------------------------
.pizza-item__type-wrap {
	margin-bottom: 30px;
	font-size: rem(20);
	line-height: 26px;
	text-transform: uppercase;
	color: #2b2c2e;
	text-align: center;

	&:after {
		content: '';
		display: block;
		width: 49px;
		height: 2px;
		background-color: #34a532;
		margin: 0 auto;
	}
}

.pizza-item__type-name  {
	font-family: $semi;
}

.pizza-item__type {
	font-family: $light;
	margin-bottom: 18px;
}


//   
.pizza-item__name-mobile {
	display: none;
	text-align: left;
	font-family: $reg;
	.bold {
		font-family: $semi;
	}
}


// ###  ###
//--------------------------------------
.pizza-item__price-wrap {
	display: flex;
	align-items: center;
	margin-bottom: 20px;
}

.pizza-item__price-discount {
	margin-right: 20px;
	font-family: $light;
	font-size: rem(32);
	line-height: 26px;
	color: $green;
	text-transform: uppercase;
	vertical-align: middle;
}

.pizza-item__price {
	font-family: $light;
	font-size: rem(22);
	line-height: 26px;
	color: #8b8b8b;
	text-transform: uppercase;
	vertical-align: middle;
}


// ###  ###
//--------------------------------------
.pizza-item__piece-count-wrap {
	display: flex;
	align-items: center;
	margin-bottom: 20px;
}

.pizza-item__piece-count {


	.count-fild {
		display: inline-block;
		width: 40px;
		height: 36px;
		margin: 0 7px;
		font-size: rem(20);
		line-height: 26px;

		background-color: white;
		border: 1px solid #b9b9b9;
		border-radius: 16px;
		color: #2b2c2e;
		text-align: center;
		text-transform: uppercase;
		vertical-align: middle;
		outline: none;
	}
	
	.count-minus,
	.count-plus {
		display: inline-block;
		width: 20px;
		height: 20px;
		border: none;
		background-color: transparent;
		cursor: pointer;
		vertical-align: middle;
		outline: none;
		border-radius: 50%;
		transition: all 100ms ease-in;
		user-select: none;


		&:hover,
		&:focus {
			background-color: $green;
		}
	}

	.count-plus {
		@include sprite($icon-plus);
	}

	.count-minus {
		@include sprite($icon-minus);
	}
}



// ###   ###
//--------------------------------------
.pizza-item__total-price-wrap {
	display: flex;
	align-items: center;
}

.pizza-item__total-price {
	font-size: rem(26);
	line-height: 26px;
	color: #202020;
	text-transform: uppercase;
}




// ###   ###
//--------------------------------------
.pizza-content__button-wrap {
	text-align: center;
}





//--------------------------------------
// ### MEDIA QUERIES ###
//--------------------------------------
@include breakpoint($wide) {


	//--------------------------------------
	.pizza-item__img {
		width: 318px;
		position: relative;
		text-align: center;
		margin-bottom: 20px;
	}

	//--------------------------------------
	.pizza-item__type-wrap {
		margin-bottom: 20px;
	}


}


@include breakpoint($desktop) { 
	

	.pizza-item__img {
		width: 280px;
		height: 280px;
	}


}

@include breakpoint($tablet) {

	.pizza-item__img {
		width: 310px;
		height: 310px;
	}



	.pizza-item__text {
		margin-right: 20px;
		font-size: rem(17);
	
		&.text--m-right-small {
			margin-right: 9px;
		}
	}



	// ### Item ###
	//--------------------------------------
	.pizza-item {
		margin-bottom: 20px;
		padding: 30px 10px;
		@include span(6);
	}


	// ###  ###
	//--------------------------------------
	.pizza-item__piece-count {
		.count-fild {
			width: 36px;
			margin: 0 6px;
			font-size: rem(18);
		}
	}

}


@include breakpoint($mobile) {

	.pizza-content {
		padding-bottom: 30px;
	}


	.pizza-item__img {
		// display: none;
		max-width: 100px;
		max-height: 100px;
	}

	//--------------------------------------
	.pizza-item__price-count-wrap {
		max-width: auto;
		width: auto;
		margin: 0;
	}

		
	.pizza-item__text {
		margin-right: 20px;
		font-size: rem(14);
	
		&.text--m-right-small {
			margin-right: 9px;
		}
	}


	// ###  ###
	//--------------------------------------
	.pizza-list {
		margin-bottom: 30px;
	}


	
	// ### Item ###
	//--------------------------------------
	.pizza-item {
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 10px;
		padding: 20px 10px;
		width: 100%;
		margin-right: 0;
		margin-left: 0;
	}



	// ###  ###
	//--------------------------------------
	.pizza-item__img-wrap {
		width: 36%;
	}

	.pizza-item__img {
		margin-right: 15px;
		margin-bottom: 0px;

	}


	//--------------------------------------
	// ###     ###
	//--------------------------------------
	.pizza-item__descr-wrap {
		width: 64%;

	}


	// ###  ###
	//--------------------------------------
	.pizza-item__type-wrap {
		margin-bottom: 10px;
		font-size: rem(14);
		line-height: 20px;
		display: flex;
		&:after {
			display: none;
		}
	}

	.pizza-item__type-name,
	.pizza-item__type {
		display: none;
	}

	.pizza-item__name-mobile {
		display: block;
	}
		
	

	// ###  ###
	//--------------------------------------
	.pizza-item__price-wrap {
		margin-bottom: 2px;
	}

	.pizza-item__price-discount {
		margin-right: 20px;
		font-size: rem(20);
		color: $green;
	}

	.pizza-item__price {
		font-size: rem(16);
	}


	// ###  ###
	//--------------------------------------
	.pizza-item__piece-count-wrap {
		margin-bottom: 2px;
	}

	.pizza-item__piece-count {
		.count-fild {
			width: 31px;
			height: 27px;
			margin: 0 4px;
			font-size: rem(16);
			line-height: 26px;
			border-radius: 12px;
		}
	}

	// ###   ###
	//--------------------------------------
	.pizza-item__total-price {
		font-size: rem(16);
	}


}


