/* 
	-----------------------------------------------
	MODAL MESSAGE
	-----------------------------------------------
*/

.pizza-modal-message {
	width: 320px;
	// width: 100%;
	margin: auto;
	padding-left: 30px;
	padding-bottom: 35px;
	background-color: #fff;
}

.pizza-modal-message__top {
	display: flex;
	justify-content: space-between;
	padding-bottom: 25px;

	.pizza-icon-wrap {
		padding-top: 15px;
		.icon {
			@include sprite($icon-pizza);
		}
	}

	.button-close-wrap {
		width: 70px;
		height: 70px;
		background-color: $red;
		position: relative;
		display: flex;
	}

	.remodal-close {
		margin: auto;
		background-color: transparent;
		border: none;
		cursor: pointer;
		outline: none;
		@include sprite($icon-close);
	}

}


.pizza-modal-message__title {
	color: #222;
	font-family: $light;
	font-size: rem(26);
	line-height: 18px;
	text-transform: uppercase;
	text-align: left;

	.bold {
		display: inline-block;
		padding-top: 12px;
		font-family: $semi;
	}
}