/* 
	-----------------------------------------------
	FOOTER
	-----------------------------------------------
*/


.main-footer {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: #26292c;
}


.main-footer-wrap {

}


// ###  ###
//--------------------------------------
.main-footer__logo {
	margin-bottom: 25px;
	text-align: center;
}



// ### // ###
//--------------------------------------
.main-footer__social-phone-button-wrap {
	display: flex;
	justify-content: center;
	align-items: center;
}


// ###  ###
//--------------------------------------
.main-footer__phone-wrap {
	margin-right: 25px;
	margin-left: 45px;
}

.main-footer__phone-icon {
	display: inline-block;
	margin-right: 10px;
	vertical-align: middle;
	@include sprite($icon-ic-phone);
}

.main-footer__phone {
	display: inline-block;
	font-family: $semi;
	font-size: rem(20);
	color: #fff;
	text-transform: uppercase;
	text-decoration: none;
	vertical-align: middle;
}




//--------------------------------------
// ### MEDIA QUERIES ###
//--------------------------------------
@include breakpoint($mobile) {

	.main-footer {
		padding-top: 20px;
		padding-bottom: 20px;
	}


	// ###  ###
	//--------------------------------------
	.main-footer__logo {
		margin-bottom: 20px;
	}


	// ### // ###
	//--------------------------------------
	.main-footer__social-phone-button-wrap {
		display: flex;
		flex-direction: column;
		// justify-content: center;
		// align-items: center;
	}
	
	// ###  ###
	//--------------------------------------
	.main-footer__phone-wrap {
		order: 1;

		margin-left: 0;
		margin-right: 0;
		margin-bottom: 20px;
	}
	
	// ###  ###
	//--------------------------------------
	.footer-social {
		order: 3;
	}


	// ###  ###
	//--------------------------------------
	.main-footer__button-wrap {
		order: 2;
		margin-bottom: 20px;
	}

}